import React, {useState} from "react";
import SEO from "../components/seo";
import _JSXStyle from 'styled-jsx/style';
import allResumeData from '../content/resume.yaml';

const Role = ({role}) => (
  <div>
    <span>
      <h4 className="di mv0">{role.title}</h4>
      <span aria-hidden="true">,</span> {role.dates}
    </span>
    <p className="mv0">{role.description}</p>
    {role.highlights ? (
      <ul className="mv0">
        {role.highlights.map((highlight, i) => (
          <li key={i}>{highlight}</li>
        ))}
      </ul>
    ) : null}
  </div>

);

const OrgExperience = ({experience}) => (
  <>
    <h3 className="mv1">{experience.org}</h3>
    {experience.roles.map((role, i) => (
      <Role role={role} key={i}/>
    ))}
  </>
);

const Education = ({education}) => (
  <>
   <h4 className="mv1" aria-level={3}>{education.school}</h4>
   <span className="db">{education.degree}</span>
   {education.honors ? <span className="db">{education.honors.join(', ')}</span> : null}
  </>

);

const Skills = ({skills}) => (
  <>
    <h4 className="mv1" aria-level={3}>{skills.title}</h4>
    {skills.highlights.join(', ')}
  </>
);

const Resume = ({data}) => {
  return  (
    <>
      <SEO title="Resume" />

      <header>
        <h1 className="ma0 pa0 f3">
          Cordelia McGee-Tubb
        </h1>
        <div className="line"></div>
      </header>

      <main>

        <div>
          {allResumeData.about.map((paragraph, i) => (
            <p key={i}>{paragraph}</p>
          ))}
        </div>

        <p>
          {allResumeData.contact.map((contact, i) => (
            <span className="db" key={i}>
              <span className="b">{contact.type}: </span>
              {contact.url ? (<a href={contact.url}>{contact.label}</a>) : contact.label}
            </span>
          ))}
        </p>

        <div>
          <section>
            <h2>Work Experience</h2>
            {allResumeData.work.map((experience, i) => (
              <OrgExperience experience={experience} key={i} />
            ))}
          </section>
        </div>

        <div>
          <section>
            <h2>Skills</h2>
             {allResumeData.skills.map((skills, i) => (
              <Skills skills={skills} key={i}/>
            ))}
          </section>

          <section>
            <h2>Education</h2>
            {allResumeData.education.map((education, i) => (
              <Education education={education} key={i}/>
            ))}
          </section>

          <section>
            <h2>Volunteer Experience</h2>
            {allResumeData.volunteer.map((experience, i) => (
              <OrgExperience experience={experience} key={i} />
            ))}
          </section>
        </div>

      </main>

      <style jsx>{`

        .line {
          background-color: #5D3FD3;
          height: .25rem;
        }

        header {
          padding: 2rem;
          padding-bottom: 0;
          display: grid;
          grid-gap: 2rem;
          grid-template-columns: auto 1fr;
          align-items: center;
        }

        main {
          padding: 2rem;
          display: grid;
          grid-template-columns: 2fr 1fr;
          grid-gap: 0 1.5rem;
          align-items: start;
        }

        a {
          font-weight: normal;
          color: #5D3FD3;
          text-decoration: underline;
        }

        h2 {
          color: #5D3FD3;
        }

        @media print {
          header {
            padding: 0;
          }

          main {
            font-size: .75rem;
            padding: 0;
          }
        }
      `}
      </style>
    </>
  );
};

export default Resume;